export default () => {
    $(document).ready(function () {
        initForm();

        function initForm() {
            $('body').on('submit', 'form', function (e) {
                var t = $(this);
                if (!isFormValidate(t)) {
                    e.preventDefault();
                    e.stopPropagation();
                    $('.error', t).first().focus();
                } else if (t.hasClass('js-ajax-form')) {
                    e.preventDefault();
                    t.closest('.js-ajax-form');
                    t.addClass('load');

                    // formSendResult(t);

                    sendFormAjax(t, function (data) {
                        formSendResult(t, data);
                        t.removeClass('load');
                    });
                }
            });

            $('body').on('change', '.error', function () {
                var t = $(this);

                if (isFieldValidate(t)) {
                    t.closest('.error').removeClass('has-error');
                }
            });
        }

        /**
         * Валидация E-mail адреса
         * @param {string} emailAddress - e-mail для проверки
         * @returns {Boolean}
         */
        function isValidEmail(emailAddress) {
            var pattern = new RegExp(
                /^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i
            );
            return pattern.test(emailAddress);
        }

        /**
         * Валидация всей формы
         * @param {object} form jQuery объект формы
         * @param {string} error_class класс ошибки
         * @returns {Boolean}
         */
        function isFormValidate(form, error_class) {
            var result = true,
                rq = $('.required', form).length,
                check = [
                    'input[type="text"]',
                    'input[type="login"]',
                    'input[type="password"]',
                    'input[type="number"]',
                    'input[type="checkbox"]',
                    'input[type="tel"]',
                    'input[type="email"]',
                    'input[type="textarea"]',
                    'input[type="select"]',
                    'textarea',
                    'select',
                ],
                parent;

            error_class = error_class || 'has-error';

            $('.required, input, textarea, select').removeClass(error_class);

            if (rq < 1) {
                return result;
            }

            for (var i = 0; i < rq; i++) {
                parent = $('.required', form).eq(i);

                $(check.join(','), parent).each(function () {
                    if (!isFieldValidate($(this), error_class)) {
                        return (result = false);
                    }
                });
            }

            return result;
        }

        /**
         * Проверка валидации поля
         * @param {object} field jQuery объект поля формы
         * @param {string} error_class класс ошибки
         * @returns {Boolean}
         */
        function isFieldValidate(field, error_class) {
            let result = true;
            let val = '';

            if (field && field.attr('name')) {
                if (!field.val()) {
                    field.val('');
                    return false;
                }

                val = (field.val() + '').trim();

                if (field.attr('name') === 'email' && !isValidEmail(val)) {
                    result = false;
                } else if (field.attr('type') === 'tel' && !isValidPhone(val)) {
                    result = false;
                } else if (
                    field.attr('type') === 'checkbox' &&
                    !field.is(':checked')
                ) {
                    result = false;
                } else if (val === null || val === '') {
                    field.val('');
                    result = false;
                }
            }

            return result;
        }

        /**
         * Валидация phone адреса
         * @param {string} phine - e-mail для проверки
         * @returns {Boolean}
         */
        function isValidPhone(phone) {
            let pattern = new RegExp(/\+7 \(\d{3}\) \d{3}-\d{2}-\d{2}/i);
            return pattern.test(phone);
        }

        /**
         * Проверяем значение на null и undefined
         * @param {mixed} val значение
         * @returns {Boolean}
         */
        function notNull(val) {
            return val !== null && val !== undefined;
        }

        /**
         * Отправляем форму ajax
         * @param {object} form jQuery объект формы
         * @param {function} callback функция обратного вызова
         */
        function sendFormAjax(form, callback) {
            sendAjax(
                form.attr('action') || './send.php',
                form.serialize(),
                callback
            );
        }

        /**
         * Отправляем ajax запрос
         * @param {string} url ссылка
         * @param {object} data данные
         * @param {function} callback функция обратного вызова
         */
        function sendAjax(url, data, callback) {
            callback = callback || function () { };

            $.ajax({
                url: url,
                dataType: 'json',
                type: 'POST',
                data: data,
                success: function (data) {
                    callback(data);
                },
                error: function (data) {
                    callback({
                        'type': 'error',
                        'class': 'danger',
                        'text': data['responseText'],
                    });
                },
            });
        }

        /**
         * Обработка отправки формы
         * @param {object} form jQuery объект формы
         * @param {object} data данные полученные от сервера
         */
        function formSendResult(form, data) {


            form.next().show();
            form.html(
                '<p class="boocking__text">Спасибо! Ваша заявка успешно отправлена.</br>Наши менеджеры свяжутся с Вами в ближайшее время.</p>'
            );
            // form.hide();
            //ym(0, 'reachGoal', 'lead');
            //ga('send','event','form','lead');
            //цель отправка формы
            //reachGoal(form.data('goal'), '');
        }

        function arrayToDict(arr) {
            let dict = {};

            for (let i in arr) {
                dict[arr[i]['name']] = arr[i]['value'];
            }

            return dict;
        }

        /**
         * Достижение цели
         * @param {string} goal название цели
         * @param {string} added дополнительная информация
         */
        function reachGoal(goal, added) {
            reachGoalYM(goal, added);
            reachGoalGA(goal);
        }

        /**
         * Отправка цели в Метрику
         * @param {string} goal название цели
         * @param {string} added дополнительная информация
         */
        function reachGoalYM(goal, added) {
            if (window.yaCounter000) {
                window.yaCounter000.reachGoal(goal, added || {});
            }
        }

        /**
         * Отправка цели в Аналитику
         * @param {string} goal название цели
         */
        function reachGoalGA(goal) {
            if (window.gtag) {
                gtag('event', 'event_name', { 'event_category': goal });
            }
        }

        /**
         * Заказ обратного звонка CallTouch
         * @param {string} formKey ключ формы
         * @param {string} phone номер телефона
         */
        function calltouchRecall(formKey, phone) {
            // создаем звонок
            window.ctw.createRequest(
                formKey,
                phone,
                [],
                function (success, data) {
                    console.log(success, data);
                    if (success) {
                        console.log(
                            'Создана заявка на колбек, идентификатор: ' +
                            data.callbackRequestId
                        );
                    } else {
                        switch (data.type) {
                            case 'request_throttle_timeout':
                            case 'request_throttle_count':
                                console.log(
                                    'Достигнут лимит создания заявок, попробуйте позже'
                                );
                                break;
                            case 'request_phone_blacklisted':
                                console.log(
                                    'номер телефона находится в черном списке'
                                );
                                break;
                            case 'validation_error':
                                console.log(
                                    'были переданы некорректные данные'
                                );
                                break;
                            default:
                                console.log(
                                    'Во время выполнения запроса произошла ошибка: ' +
                                    data.type
                                );
                        }
                    }
                }
            );
        }

        /**
     * Создание завяки для обратного звонка Calltouch
     * @param {object} data данные формы
     */
        function calltouchRequest(phone, key) {
            if (window.ctw) {
                window.ctw.createRequest(
                    key,
                    phone.replace(/\D+/g, ""), [],
                    function (success, data) {
                        console.log(success, data);
                        if (success) {
                            console.log(
                                "Создана заявка на колбек, идентификатор: " +
                                data.callbackRequestId
                            );
                        } else {
                            var unsent_data = [{ 'name': key, 'phone': phone.replace(/\D+/g, ""), 'error': data.type }];
                            $('input[name="request_error"]').val(unsent_data);
                            switch (data.type) {
                                case "request_throttle_timeout":
                                case "request_throttle_count":
                                    console.log(
                                        "Достигнут лимит создания заявок, попробуйте позже"
                                    );
                                    break;
                                case "request_phone_blacklisted":
                                    console.log("номер телефона находится в черном списке");
                                    break;
                                case "validation_error":
                                    console.log("были переданы некорректные данные");
                                    break;
                                default:
                                    console.log(
                                        "Во время выполнения запроса произошла ошибка: " +
                                        data.type
                                    );
                            }
                        }
                    }
                );
            }
        }

        // Вызов модалки CallTouch
        $('[data-src="#calltouch"]').on('click', function () {
            window.ct(
                'modules',
                'widgets',
                'openExternal',
                'promotion',
                function (result) {
                    console.log(result);
                }
            );
        });
    });
};
