$(function() {
    const $commentSlider = $('.js-comment-slider')

    $commentSlider.slick({
        slidesToShow: 3,
        slidesToScroll: 1,
        arrows: false,
        responsive: [
            {
                breakpoint: 1440,
                settings: {
                    slidesToShow: 2,
                },
            },
            {
                
                breakpoint: 768,
                settings: {
                    slidesToShow: 1,
                    variableWidth: true,
                }
            },
      ]
    });

    $(".js-comment-text").text(function(i, text) {

        if (text.length >= 150) {
          text = text.substring(0, 150);
          var lastIndex = text.lastIndexOf(" ");
          text = text.substring(0, lastIndex) + '...';
        }
        
        $(this).text(text);
    });
})