$(function () {
    const $newsSlider = $('.js-news-slider')


    const width = $(window).width()
    if (width < 1250) {
        $newsSlider.slick({
            slidesToShow: 3,
            slidesToScroll: 1,
            arrows: false,
            responsive: [
                {
                    breakpoint: 768,
                    settings: {
                        slidesToShow: 1,
                        variableWidth: true,
                    }
                },
            ]
        });
    }
})