$(function () {

    const mapId = $('#ymap');

    function init() {

        const map = new ymaps.Map('ymap', {
            center: [55.67450294928798, 37.022896335937396],
            zoom: 9,
            controls: ['fullscreenControl', 'zoomControl', 'trafficControl'],
            behaviors: ['drag', 'dblClickZoom', 'multiTouch']
        });

        const placemark1 = new ymaps.Placemark([55.468045, 37.559236], {
            hintContent: mapId.data('content')
        }, {
            iconLayout: 'default#image',
            iconImageHref: '/dist/img/pin.png',
            iconImageSize: [53, 70],
            iconImageOffset: [-25, -70],
        });
        const placemark2 = new ymaps.Placemark([55.881394, 37.732718], {
            hintContent: mapId.data('content')
        }, {
            iconLayout: 'default#image',
            iconImageHref: '/dist/img/pin.png',
            iconImageSize: [53, 70],
            iconImageOffset: [-25, -70],
        });

        const width = $(window).width()
        if (width < 770) {
            ynewMap.behaviors.disable('drag');
        }

        map.geoObjects
            .add(placemark1)
            .add(placemark2);
    }

    ymaps.ready(init);
})